@import "./src/Common/Styles/Sass/variables.scss";
@import "./src/Common/Styles/Sass/mixins";

.footerBlock {
  display: flex;
  height: content-box;
  background-color: $FooterColor;
  justify-content: center;
}

.footerContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  width: 100%;
  max-width: 1440px;
}

.titleBlock {

}

.someBlockWrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 350px;
  @media screen and (max-width: 400px) {
    max-width: 260px;
  }

  .someBlock {
    a {
      img {
        width: 50px;
        height: 50px;
        @media screen and (max-width: 400px) {
          width: 35px;
          height: 35px;
        }
      }
    }
  }
}


.allRight {
  display: flex;

  a {
    text-decoration: none;
    @include textForAll($montserrat, 16px, 700, $forIcons);
    letter-spacing: 1.1px;
    opacity: 0.9;
  }
}